<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <div class="ucTitNav ofh">
                            <span>发票管理</span>
<!--                            <el-button type="primary" @click="dialogFormVisible = true" plain>+ 添加发票</el-button>-->
                        </div>
                        <el-table class="invTable"
                                  :data="tableData"
                                  style="width: 100%;">
                            <el-table-column type="expand">
                                <template slot-scope="props">
                                    <el-form label-position="left" inline class="table-expand">
                                        <el-form-item label="类型">
                                            <span>{{ props.row.type==0?'企业':'个人'}}</span>
                                        </el-form-item>
                                        <el-form-item label="发票抬头">
                                            <span>{{props.row.type==0?props.row.ename:props.row.title}}</span>
                                        </el-form-item>
                                        <el-form-item label="公司税号" v-show="props.row.type==0">
                                            <span>{{ props.row.taxnumber }}</span>
                                        </el-form-item>
                                        <el-form-item label="开户银行" v-show="props.row.type==0">
                                            <span>{{ props.row.bank}}</span>
                                        </el-form-item>
                                        <el-form-item label="银行账号" v-show="props.row.type==0">
                                            <span>{{ props.row.banknumber}}</span>
                                        </el-form-item>
                                        <el-form-item label="地址">
                                            <span>{{ props.row.address }}</span>
                                        </el-form-item>
                                        <el-form-item label="电话">
                                            <span>{{ props.row.phone}}</span>
                                        </el-form-item>
                                    </el-form>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    label="类型"
                                    prop="type"
                            >
                                <template slot-scope="scope">
                                    {{scope.row.type==0?'企业':'个人'}}
                                </template>
                            </el-table-column>
                            <el-table-column
                                    label="发票抬头"
                                    >
                                <template slot-scope="scope">
                                {{scope.row.type==0?(scope.row.ename?scope.row.ename:'空'):(scope.row.title?scope.row.title:'空')}}
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="150">
                                <template slot-scope="scope">
                                    <el-row>
                                        <el-button size="small" icon="el-icon-edit" @click="editInvoice(scope.row.invoice_id?scope.row.invoice_id:'',scope.row.type)">编辑</el-button>
                                    </el-row>
                                </template>
                            </el-table-column>
                        </el-table>

                        <!-- 弹框 -->
                        <el-dialog title="填写发票信息" :visible.sync="dialogFormVisible">
                            <div class="m_sel">
                                <div class="m_tit">发票类型：</div>
                                <div class="m_choice">
<!--                                    <el-radio-group v-model="invoiceType" size="medium">-->
<!--                                        <el-radio label="0" border>企业</el-radio>-->
<!--                                        <el-radio label="1" border>个人</el-radio>-->
<!--                                    </el-radio-group>-->
                                    {{invoiceType==0?'企业':'个人'}}
                                </div>
                            </div>
                            <div v-if="invoiceType=='0'">
                                <el-form :model="companyForm" :rules="companyRules" ref="companyForm" label-width="90px">
                                    <el-form-item label="公司名称" prop="ename">
                                        <el-input v-model="companyForm.ename"></el-input>
                                    </el-form-item>
                                    <el-form-item label="公司税号" prop="taxnumber" >
                                        <el-input v-model="companyForm.taxnumber"></el-input>
                                    </el-form-item>
                                    <el-form-item label="开票地址" prop="address" >
                                        <el-input type="textarea" v-model="companyForm.address" ></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话号码" prop="phone" >
                                        <el-input v-model="companyForm.phone"></el-input>
                                    </el-form-item>
                                    <el-form-item label="开户银行" prop="bank">
                                        <el-input v-model="companyForm.bank"></el-input>
                                    </el-form-item>
                                    <el-form-item label="银行账户" prop="banknumber" >
                                        <el-input v-model="companyForm.banknumber"></el-input>
                                    </el-form-item>
                                </el-form>
                                <div slot="footer" class="dialog-footer tc">
                                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                                    <el-button type="primary" @click="submitForm('companyForm',companyForm.invoice_id)">保 存</el-button>
                                </div>
                            </div>
                            <div v-else>
                                <el-form :model="personForm" :rules="personRules" ref="personForm"  label-width="90px">
                                    <el-form-item label="名称" prop="title">
                                        <el-input v-model="personForm.title"></el-input>
                                    </el-form-item>
                                    <el-form-item label="地址" prop="address">
                                        <el-input type="textarea" v-model="personForm.address"></el-input>
                                    </el-form-item>
                                    <el-form-item label="电话号码" prop="phone">
                                        <el-input v-model="personForm.phone" ></el-input>
                                    </el-form-item>
                                </el-form>
                                <div slot="footer" class="dialog-footer tc">
                                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                                    <el-button type="primary" @click="submitForm('personForm',personForm.invoice_id)">保 存</el-button>
                                </div>
                            </div>
                        </el-dialog>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import {invoice,addOrEditInvoice,delInvoice,editInvoiceShow} from '../../../service/api'
    import headTop from '../../../components/header/headTop';
    export default {
        name: 'invoice',
        data(){
            return{
                tableData: [],
                personForm:{
                    invoice_id:'',
                    title:'',
                    address:'',
                    phone:''
                },
                companyForm:{
                    invoice_id:'',
                    ename:'',
                    taxnumber:'',
                    address:'',
                    phone:'',
                    bank:'',
                    banknumber:''
                },
                dialogFormVisible: false,
                invoiceType:'0',//0是企业，1是个人
                personRules:{
                    title: [
                        {required:true,message:"请输入名称",trigger:"blur"}
                    ],
                    address: [
                        {required:true,message:"请填写地址",trigger:"blur"}
                    ],
                    phone: [
                        { required: true, message: '请输入手机号',trigger:"blur"},
                    ]
                },
                companyRules:{
                    ename: [
                        {required:true, message:"请输入公司名称",trigger:"blur"}
                    ],
                    taxnumber: [
                        { required: true, message: '请输入公司税号',trigger:"blur"},
                    ],
                    address: [
                        {required:true, message:"请填写开票地址",trigger:"blur"}
                    ],
                    phone: [
                        { required: true, message: '请输入电话号码',trigger:"blur"},
                    ],
                    bank:[
                        { required: true, message: '请输入开户银行',trigger:"blur"},
                    ],
                    banknumber:[
                        { required: true, message: '请输入银行账户',trigger:"blur"},
                    ]
                }
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.initData();

        },
        computed:{

        },
        methods:{
            async initData(){
                // 获取表格数据
                var res = await invoice();
                this.login = res.status==12007?false:true;
                if(res){
                    if(res.status != 12007){
                        this.calTable(res);
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            calTable(res){
                var obj = [
                    {
                        invoice_id:'',
                        type:0,
                        title:"",
                        ename:"",
                        taxnumber:"",
                        bank:"",
                        address:"",
                        phone:""
                    },
                    {
                        invoice_id:'',
                        type:1,
                        title:"",
                        ename:"",
                        taxnumber:"",
                        bank:"",
                        address:"",
                        phone:""
                    }
                ];
                for(var i = 0 ; i < res.length ; i++){
                    if(res[i].type == 0){
                        obj[0] = res[i];
                    }else{
                        obj[1] = res[i];
                    }
                }
                this.tableData = obj;
            },
            choiceinvoiceType:function(tar){
                this.invoiceType = tar;
            },
            submitForm(formName,id) {
                id = id || '';
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.invoiceType=='0'){//0是企业；1是个人
                            var res = await addOrEditInvoice(
                                id,//invoice_id
                                '',//title
                                this.invoiceType,//type
                                this.companyForm.taxnumber,//taxnumber
                                this.companyForm.address,//address
                                this.companyForm.banknumber,//banknumber
                                this.companyForm.phone,//phone
                                this.companyForm.bank,//bank
                                this.companyForm.ename//ename
                            )
                        }else{
                            var res = await addOrEditInvoice(
                                id,
                                this.personForm.title,
                                this.invoiceType,//type
                                '',//taxnumber
                                this.personForm.address,//address
                                '',//banknumber
                                this.personForm.phone,//phone
                                '',//bank
                                ''//ename
                            )
                        }
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                var newRes = await invoice();
                                this.calTable(newRes);
                                this.dialogFormVisible = false;
                            }
                        }
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            async editInvoice(invoice_id,type){
                console.log(type);
                this.invoiceType = String(type);
                if(invoice_id){//编辑发票
                    var res = await editInvoiceShow(
                        invoice_id
                    );
                    if(res.type == 0){//0是企业，1是个人
                        this.companyForm.invoice_id = res.invoice_id;
                        this.companyForm.ename = res.ename;
                        this.companyForm.taxnumber = res.taxnumber;
                        this.companyForm.address = res.address;
                        this.companyForm.phone = res.phone;
                        this.companyForm.bank = res.bank;
                        this.companyForm.banknumber = res.banknumber;
                    }else{
                        this.personForm.invoice_id = res.invoice_id;
                        this.personForm.title = res.title;
                        this.personForm.address = res.address;
                        this.personForm.phone = res.phone;
                    }
                    this.dialogFormVisible = true;
                }else{
                    //新建发票
                    this.dialogFormVisible = true;
                }
            }


        },
        created(){

        },

    }

</script>

<style scoped>
    /*发票管理*/

    .invTable{display:block;width:100%;}
    .ucTitNav{
        display:flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .ucTitNav span{
        font-size: 16px;
        font-weight: bold;
        color: #333;
        padding-left: 10px;
        border-left: 5px solid #0bb8b2;
        margin:5px;
        line-height: 30px;
    }

    .table-expand {
        font-size: 0;
    }
    .table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .m_sel{
        display:flex;
        flex-wrap: nowrap;
        font-size:14px;
        margin-bottom: 10px;
    }
    .m_tit{width:90px;}


</style>



